.unselectable {	
    -moz-user-select: none;	
    -khtml-user-select: none;	
    -webkit-user-select: none;	
    user-select: none;	
  }	
  
  .selectabe {	
    -moz-user-select: all;	
    -khtml-user-select: all;	
    -webkit-user-select: all;	
    user-select: all;	
  }	
  
   .container {
    width: 75%;	
    margin-left: 15%;	
    } 
.demo-infinite-container {
  border: 1px solid #e8e8e8;
  border-radius: 4px;
  overflow: auto;
  padding: 8px 24px;
  height: 300px;
}
.demo-loading-container {
  position: absolute;
  bottom: 40px;
  width: 100%;
  text-align: center;
}